<script>

import navbar from './navbar.vue'
import educ from './education.vue'
//import forms from './form.vue'
import tab from './tab.vue'
import worktab from './work_tab.vue'
import sidebar from './sidebar.vue'



export default {

  name: 'Profile',
  props: {
    msg: String
  },
  methods:{
    toggleOpenAll: function(){
      if(this.openall){
        this.openall = false
        this.open_all_text = 'Expand All'
      }
      else{
        this.openall = true
        this.open_all_text = 'Collapse All'
      }

    }
    
  },
 
  components: {
    navbar,
    tab,
    worktab,
    sidebar,
    educ, 
    //forms
 

  },
  created(){
    
    const Http = new XMLHttpRequest();
    const url='https://spreadsheets.google.com/feeds/cells/1OBW5sudqT3aKfaCfAw7eLvTRGUMscNPY8zw1vm_YaZE/3/public/full?alt=json';
    Http.open("GET", url);
    Http.send();

    Http.onreadystatechange = (e) => {
      var obj = JSON.parse(Http.response)
      console.log(e)
      
      var dict = {}
      var entries = obj.feed.gs$colCount.$t
      //console.log(entries)
      for(var i  = 0; i < entries; i++){
        var content = obj.feed.entry[i].content.$t.toString()
        
       if(parseInt(i) < parseInt(entries)-3){
         //console.log(obj.feed.entry[parseInt(i) + parseInt(entries)].content.$t)    
         dict[content] = obj.feed.entry[parseInt(i) + parseInt(entries)].content.$t
       }
      }
      
      var numjobs = parseInt(dict["numjobs"])
      var numproj = parseInt(dict["numproj"])
      //console.log(numjobs)
      for (var u = 0; u < numjobs; u ++){
        var k = numjobs - u 
        this.work.push({})
        this.work[u].name = dict["jobtitle" + (k)]
        this.work[u].sub = dict["organisation" + (k)]
        this.work[u].startdate= dict["date" + (k)]
        if(dict["desc" + (k)+ "b3"] == "-"){
          this.work[u].desc = [dict["desc" + (k) + "b1"],dict["desc" + (k)+ "b2"]]
        }
        else{
          this.work[u].desc = [dict["desc" + (k) + "b1"],dict["desc" + (k)+ "b2"], dict["desc" + (k)+ "b3"]]
        }
        this.work[u].icourl = dict["pic" + (k)]
        
        this.work[u].skills = JSON.parse(dict["skill" + (k)]);
        this.work[u].enddate = ""
        if(u == numjobs - 1){
          this.done = true
        }
      }


      for (var j = 0; j < numproj; j ++){
        k = j+1
        console.log("test")
        this.projs.push({})
        this.projs[j].name = dict["proj" + (k)]
        
        this.projs[j].desc = dict["projdesc" + (k)]
    
        this.projs[j].icourl = dict["projpic" + (k)]
        this.projs[j].link = dict["projlink" + (k)]
        
        this.projs[j].skills = JSON.parse(dict["skill" + (k)]);
        this.projs[j].enddate = ""
      }



      //this.dict = true
      this.link = obj.feed.entry[parseInt(entries)*2-3].content.$t
      this.cap = dict['gpa']
      
      
}
  },
  data(){
    return {
      link:"",
      done:false,
      dict:false,
      projects: [1,2,3,4],
      work:[],
      projs:[],
      cap:"",
      engineersubtitle : "Hey there, I'm Reon.",
      fadeInElements:[],
      openall : false,
      open_all_text: 'Expand All'
    }
  }

}
</script>

